* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.header {
  width: 100%;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  background-image: url(../img/bg-header.jpg);
}
.body__header {
  height: 100%;
}
.row__header .foto__header {
  border-radius: 15px;
}

.img__Slider {
  width: 100%;
}
.row__header {
  padding: 25px;
  height: 100vh;
}
.titulo__h1 {
  color: #fff;
  font-family: "Fredericka the Great", cursive;
  font-size: 130px;
}
.titulo__confecciones {
  font-family: "Dosis", sans-serif;
  font-size: 67px;
  color: #fff;
}
.img__Slider,
h3 {
  font-family: "Sulphur Point", sans-serif;
  letter-spacing: 20px;
}
.img__Slider,
p {
  font-family: "Fredericka the Great", cursive;
  font-size: 30px;
}
.header .sub__titulo__header {
  color: #fff;
  font-size: 18px;
  letter-spacing: 10px;
  margin-top: 0;
  margin-bottom: 30px;
  text-transform: capitalize;
  font-weight: 500;
  font-family: "Sulphur Point", sans-serif;
}
.list-unstyled {
  padding-left: 0;
  list-style: none;
}
.list-unstyled a {
  color: #fff;
  font-size: 25px;
}
.list-unstyled li {
  float: left;
  margin-right: 50px;
}
@media (min-width: 1910px) {
  .img__Slider {
    width: 100%;
  }
  .titulo__h1 {
    font-family: "Fredericka the Great", cursive;
    font-size: 210px;
  }
  .titulo__confecciones {
    font-family: "Dosis", sans-serif;
    font-size: 110px;
  }
  .header .sub__titulo__header {
    font-size: 32px;
    letter-spacing: 10px;
  }
  .list-unstyled a {
    font-size: 35px;
  }
  .row__header .foto__header {
    width: 75%;
  }
}
@media (max-width: 900px) {
  .titulo__h1 {
    font-size: 80px;
  }
  .titulo__confecciones {
    font-size: 41px;
  }
  .header .sub__titulo__header {
    font-size: 12px;
    letter-spacing: 5px;
  }
  .list-unstyled a {
    font-size: 20px;
  }
  .list-unstyled li {
    float: left;
    margin-right: 36px;
  }
  @media (max-width: 500px) {
    .row__header {
      height: 100%;
    }
  }
}
